body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: #333;
  color: #fff;
}

nav a, .social-links a {
  margin: 0 1rem;
  color: #fff;
  text-decoration: none;
}

section {
  padding: 2rem;
}

h2 {
  border-bottom: 2px solid #333;
}

.project {
  margin-bottom: 1rem;
}

