/* Projects Container */
#projects {
  padding: 2rem;
  background-color: #f5f5f5;
  color: #333;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

/* Project Cards Container */
.project-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

/* Individual Project Card */
.project-card {
  background: #ffffff;
  width: 300px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  padding: 1.5rem;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Project Card Hover Effect */
.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

/* Project Title Styling */
.project-card h3 {
  font-size: 1.25rem;
  color: #4A90E2;
  margin-bottom: 0.5rem;
}

/* Project Description */
.project-card p {
  font-size: 1rem;
  line-height: 1.5;
  color: #666;
  margin-bottom: 1rem;
}

/* Key Learnings or Skills Gained */
.project-card strong {
  color: #333;
  font-weight: bold;
}

/* Button to View Project Details */
.project-card a {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #4A90E2;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  text-align: center;
}

.project-card a:hover {
  background-color: #357ABD;
}

/* Responsive Design for Smaller Screens */
@media (max-width: 768px) {
  .project-cards {
    flex-direction: column;
    align-items: center;
  }
}

/*About Me CSS code */

#about {
  padding: 2rem;
  background-color: #f5f5f5;
  color: #333;
  border-radius: 8px;
  max-width: 800px;
  margin: 0 auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

#about h2 {
  font-size: 2.5rem;
  color: #4A90E2;
  text-align: center;
  margin-bottom: 1.5rem;
}

#about p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

#about h3 {
  font-size: 1.8rem;
  color: #333;
  margin-top: 1.5rem;
  border-bottom: 2px solid #4A90E2;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
}

#about ul {
  list-style-type: none;
  padding: 0;
}

#about ul li {
  font-size: 1.1rem;
  margin: 0.5rem 0;
  padding: 0.3rem 0.5rem;
  border-left: 3px solid #4A90E2;
  background: #e0f7fa;
  color: #333;
}
